import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useCallback, useEffect, useState } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';
// material
import { Box, Card, Grid, Stack, TextField } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import Request from '../../../utils/Request';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
//
import { useDispatch, useSelector } from '../../../redux/store';
import { getBoules, getInfoTirages, getAgents } from '../../../redux/slices/user';
// ----------------------------------------------------------------------

BouleNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentBoule: PropTypes.object
};

export default function BouleNewForm({ isEdit, currentBoule }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const { infotirages, agents } = useSelector((state) => state.user);

  const NewBouleSchema = Yup.object().shape({
    boule: Yup.string().required('boule is required'),
    tirage_id: Yup.string().required('tirage_id is required'),
    agent_id: Yup.string().required('agent_id is required')
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      boule: currentBoule?.boule || null,
      bouleId: currentBoule?.id || null,
      tirage_id: currentBoule?.tirage || null,
      agent_id: currentBoule?.agent || null
    },
    validationSchema: NewBouleSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      try {
        if (!isEdit) {
          await Request.handleAddBouleBlock(values);
        } else {
          await Request.handleModifyBouleBlock(values);
        }
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Créer avec succès' : 'Mettre à jour avec succès', { variant: 'success' });
        getBoules();
        navigate(PATH_DASHBOARD.borlette?.blocage_boule);
      } catch (error) {
        if (error?.message) {
          enqueueSnackbar(error?.message, { variant: 'error' });
        } else {
          alert(JSON.stringify(error));
        }
        setSubmitting(false);
        setErrors(error);
      }
    }
  });

  useEffect(() => {
    dispatch(getBoules());
    dispatch(getInfoTirages());
    dispatch(getAgents());
  }, [dispatch]);

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={8}>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    select
                    fullWidth
                    label="Tirage"
                    placeholder="Choisir tirage"
                    {...getFieldProps('tirage_id')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.tirage_id && errors.tirage_id)}
                    // helperText={touched.tirage_id && errors.tirage_id}
                  >
                    <option value="" />
                    {[{ id: 0, name: 'Tout' }, ...infotirages]?.map((optio) => (
                      <option key={optio.id} value={optio.id}>
                        {optio.name}
                      </option>
                    ))}
                  </TextField>
                  <TextField
                    select
                    fullWidth
                    label="Agent"
                    placeholder="Choisir Agent"
                    {...getFieldProps('agent_id')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.agent_id && errors.agent_id)}
                    // helperText={touched.agent_id && errors.agent_id}
                  >
                    <option value="" />
                    {[{ id: 0, first_name: 'Tout' }, ...agents]?.map((optio) => (
                      <option key={optio.id} value={optio.id}>
                        {optio.first_name} {optio.last_name}
                      </option>
                    ))}
                  </TextField>
                </Stack>
                <Stack>
                  <TextField
                    fullWidth
                    disabled={isEdit}
                    label="Boule"
                    placeholder="Entrer le boule"
                    {...getFieldProps('boule')}
                    error={Boolean(touched.boule && errors.boule)}
                    // helperText={touched.boule && errors.boule}
                  />
                </Stack>
                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                  <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                    {!isEdit ? 'Créer boule' : 'Sauvegarder les modifications'}
                  </LoadingButton>
                </Box>
              </Stack>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
