import { useEffect } from 'react';
import { paramCase } from 'change-case';
import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getBoules } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import BouleNewForm from '../../components/_dashboard/boule/BouleNewForm';

// ----------------------------------------------------------------------

export default function BouleCreate() {
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { pathname } = useLocation();
  const { bouleId } = useParams();
  const { boules } = useSelector((state) => state.user);
  const isEdit = pathname.includes('edit');
  const currentBoule = boules.find((boul) => boul?.id === Number(bouleId));

  return (
    <Page title="boule: Bloquer un nouvel boule | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Block Boule' : 'Modification boule block'}
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Boules', href: PATH_DASHBOARD.borlette?.blocage_boule },
            { name: !isEdit ? 'Block Boule' : 'Modification boule block' }
          ]}
        />

        <BouleNewForm isEdit={isEdit} currentBoule={currentBoule} />
      </Container>
    </Page>
  );
}
